                            achievement_post.data &&
                                achievement_post.data.length
                        "
                    >
                        <li
                            v-for="(post, key) in achievement_post.data"
                            :key="key"
                        >
                            <span
                                class="u-tab"
                                v-text="getTypeLabel(post.type)"
                            ></span>
                            <a
                                class="u-title"
                                target="_blank"
                                :href="post.link"
                                >{{ post.title || "Êó†Ê†áÈ¢ò" }}</a
                            >
                            <span
                                v-if="post.checked == 0"
                                class="u-mark pending"
                                >‚åõ Á≠âÂæÖÂÆ°Ê†∏</span
                            >
                            <span v-if="post.checked == 1" class="u-mark"
                                >‚úî ÂÆ°Ê†∏ÈÄöËøá</span
                            >
                            <span v-if="post.checked == 2" class="u-mark reject"
                                >‚ùå ÂÆ°Ê†∏È©≥Âõû</span
                            >
                            <span v-if="post.checked == 3" class="u-mark hold"
                                >üîê Á≠âÂæÖÈ™åËØÅ</span
                            >
                            <div class="u-desc">
                                <span
                                    v-if="
                                        post.checked == 2 && post.check_remark
                                    "
                                    class="u-check_remark"
                                    v-html="`È©≥ÂõûÁêÜÁî±Ôºö${post.check_remark}`"
                                ></span>
                                <time class="u-desc-subitem"
                                    ><i class="el-icon-finished"></i> ÂèëÂ∏É :
                                    {{
                                        new Date(post.created * 1000)
                                            | dateFormat
                                    }}</time
                                >
                                <time class="u-desc-subitem"
                                    ><i class="el-icon-refresh"></i> Êõ¥Êñ∞ :
                                    {{
                                        new Date(post.updated * 1000)
                                            | dateFormat
                                    }}</time
                                >
                            </div>

                            <el-button-group class="u-action">
                                <!-- <el-button
                                    size="mini"
                                    icon="el-icon-edit"
                                    :disabled="post.checked != 0"
                                    title="ÁºñËæë"
                                    @click="post_edit('achievement', post)"
                                ></el-button> -->
                                <el-button
                                    size="mini"
                                    icon="el-icon-delete"
                                    title="Âà†Èô§"
                                    @click="post_del(post)"
                                ></el-button>
                            </el-button-group>
                        </li>
                    </ul>
                    <el-alert
                        v-else
                        class="m-dashboard-box-null"
                        title="Ê≤°ÊúâÊâæÂà∞Áõ∏ÂÖ≥Êù°ÁõÆ"
                        type="info"
                        center
                        show-icon
                    >
                    </el-alert>
                    <el-pagination
                        class="m-dashboard-box-pages"
                        background
                        :total="achievement_post.total"
                        hide-on-single-page
                        layout="total, prev, pager, next, jumper"
                        :current-page="post_page"
                        :page-size="length"
                        @current-change="post_page_change"
                    >
                    </el-pagination>
                </div>
            </el-tab-pane>
            <el-tab-pane label="ÁôæÁßëËØÑËÆ∫" name="wiki_comment">
                <el-input
                    class="m-dashboard-work-search u-source-search"
                    placeholder="ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÜÖÂÆπ"
                    v-model="achievement_comment.keyword"
                    @change="search_comment"
                >
                    <template slot="prepend">ÂÖ≥ÈîÆËØç</template>
                    <el-button
                        slot="append"
                        icon="el-icon-search"
                        @click="search_comment"
                    ></el-button>
                </el-input>

                <div class="m-dashboard-box">
                    <ul
                        class="m-dashboard-box-list"
                        v-if="
                            achievement_comment.data &&
                                achievement_comment.data.length
                        "
                    >
                        <li
                            v-for="(comment, key) in achievement_comment.data"
                            :key="key"
                        >
                            <span
                                class="u-tab"
                                v-text="getTypeLabel(comment.type)"
                            ></span>
                            <a
                                class="u-title"
                                target="_blank"
                                :href="comment.link"
                                >{{ comment.title || "Êó†Ê†áÈ¢ò" }}</a
                            >
                            <span
                                v-if="comment.checked == 0"
                                class="u-mark pending"
                                >‚åõ Á≠âÂæÖÂÆ°Ê†∏</span
                            >
                            <span v-if="comment.checked == 1" class="u-mark"
                                >‚úî ÂÆ°Ê†∏ÈÄöËøá</span
                            >
                            <span
                                v-if="comment.checked == 2"
                                class="u-mark reject"
                                >‚ùå ÂÆ°Ê†∏È©≥Âõû</span
                            >
                            <div class="u-desc">
                                <span class="u-content"
                                    ><i class="el-icon-s-comment"></i>
                                    {{ comment.content }}</span
                                >
                                <time class="u-desc-subitem"
                                    ><i class="el-icon-finished"></i> ÂèëÂ∏É :
                                    {{
                                        new Date(comment.created * 1000)
                                            | dateFormat
                                    }}</time
                                >
                                <time class="u-desc-subitem"
                                    ><i class="el-icon-refresh"></i> Êõ¥Êñ∞ :
                                    {{
                                        new Date(comment.updated * 1000)
                                            | dateFormat
                                    }}</time
                                >
                            </div>

                            <el-button-group class="u-action">
                                <el-button
                                    type="primary"
                                    size="mini"
                                    icon="el-icon-delete"
                                    title="Âà†Èô§"
                                    @click="comment_del(comment)"
                                ></el-button>
                            </el-button-group>
                        </li>
                    </ul>
                    <el-alert
                        v-else
                        class="m-dashboard-box-null"
                        title="Ê≤°ÊúâÊâæÂà∞Áõ∏ÂÖ≥Êù°ÁõÆ"
                        type="info"
                        center
                        show-icon
                    >
                    </el-alert>
                    <el-pagination
                        class="m-dashboard-box-pages"
                        background
                        :total="achievement_comment.total"
                        hide-on-single-page
                        layout="total, prev, pager, next, jumper"
                        :current-page="comment_page"
                        :page-size="length"
                        @current-change="comment_page_change"
                    >
                    </el-pagination>
                </div>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import { getTypeLabel } from "@jx3box/jx3box-common/js/utils";
import { __wikiType } from "@jx3box/jx3box-common/data/jx3box.json";
import dateFormat from "@/utils/dateFormat";
import {
    get_posts,
    remove_post,
    get_comments,
    remove_comment,
} from "@/service/wiki";
export default {
    name: "wiki",
    props: [],
    data: function() {
        return {
            active_name: this.$route.query.type
                ? this.$route.query.type
                : "wiki_post",
            achievement_post: {
                data: null,
                total: 0,
                keyword: null,
            },
            achievement_comment: {
                data: null,
                total: 0,
                keyword: null,
            },
            post_page: 1,
            comment_page: 1,
            length: 10,
        };
    },
    methods: {
        getTypeLabel: function(val) {
            return val ? __wikiType[val] : "Êú™Áü•";
        },
        post_page_change(i = 1) {
            this.post_page = i;
            get_posts(this.achievement_post.keyword, i, this.length).then(
                (data) => {
                    data = data.data;
                    this.achievement_post.data =
                        data.code === 200 ? data.data.data : false;
                    this.achievement_post.total =
                        data.code === 200 ? data.data.total : 0;
                },
                () => {
                    this.achievement_post.data = false;
                }
            );
        },
        search_post() {
            this.post_page_change(1);
        },
        comment_page_change(i = 1) {
            this.comment_page = i;
            get_comments(this.achievement_comment.keyword, i, this.length).then(
                (data) => {
                    data = data.data;
                    this.achievement_comment.data =
                        data.code === 200 ? data.data.data : false;
                    this.achievement_comment.total =
                        data.code === 200 ? data.data.total : 0;
                },
                () => {
                    this.achievement_comment.data = false;
                }
            );
        },
        search_comment() {
            this.comment_page_change(1);
        },
        post_edit(type, post) {
            switch (type) {
                case "achievement":
                    this.$message("Âç≥Â∞ÜÂºÄÊîæ");
                    break;
            }
        },
        post_del(post) {
            this.$alert("Á°ÆÂÆöË¶ÅÂà†Èô§ÂêóÔºü", "Á°ÆËÆ§‰ø°ÊÅØ", {
                confirmButtonText: "Á°ÆÂÆö",
                callback: (action) => {
                    if (action == "confirm") {
                        remove_post(post.id).then(
                            (data) => {
                                data = data.data;
                                if (data.code === 200) {
                                    this.$notify({
                                        title: "Âà†Èô§ÊàêÂäü",
                                        type: "success",
                                    });
                                    this.post_page_change(this.post_page);
                                } else {
                                    this.$notify({
                                        title: "Âà†Èô§Â§±Ë¥•",
                                        type: "error",
                                    });
                                }
                            },
                            () => {
                                this.$notify({
                                    title: "Âà†Èô§Â§±Ë¥•",
                                    type: "error",
                                });
                            }
                        );
                    }
                },
            });
        },
        comment_del(comment) {
            this.$alert("Á°ÆÂÆöË¶ÅÂà†Èô§ÂêóÔºü", "Á°ÆËÆ§‰ø°ÊÅØ", {
                confirmButtonText: "Á°ÆÂÆö",
                callback: (action) => {
                    remove_comment(comment.id).then(
                        (data) => {
                            data = data.data;
                            if (data.code === 200) {
                                this.$notify({
                                    title: "Âà†Èô§ÊàêÂäü",
                                    type: "success",
                                });
                                this.comment_page_change(this.post_page);
                            } else {
                                this.$notify({
                                    title: "Âà†Èô§Â§±Ë¥•",
                                    type: "error",
                                });
                            }
                        },
                        () => {
                            this.$notify({ title: "Âà†Èô§Â§±Ë¥•", type: "error" });
                        }
                    );
                },
            });
        },
    },
    filters: {
        dateFormat: function(val) {
            return dateFormat(new Date(val));
        },
    },
    watch: {
        $route: {
            immediate: true,
            handler() {
                if (this.$route.query.type && this.$route.query.keyword) {
                    switch (this.$route.query.type) {
                        case "wiki_post":
                            this.achievement_post.keyword = this.$route.query.keyword;
                            break;
                        case "wiki_comment":
                            this.achievement_comment.keyword = this.$route.query.keyword;
                            break;
                    }

                    // ÁΩÆÁ©∫ËæìÂÖ•Ê°ÜID
                    this.$nextTick(() => {
                        let input_doms = document.querySelectorAll(
                            ".u-source-search input"
                        );
                        for (let i = 0; i < input_doms.length; i++)
                            input_doms[i].value = "";
                    });
                } else {
                    this.achievement_post.keyword = "";
                    this.achievement_comment.keyword = "";
                }

                // ÂàóË°®Ëé∑Âèñ
                this.post_page_change();
                this.comment_page_change();
            },
        },
    },
};
</script>

